#Messages {
    padding: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    > h4,
    > .player-controls {
        width: 100%;
        max-width: 522px;
        text-align: right;
        margin-bottom: 10px;
        > .highlight {
            border-bottom: 1px dashed gray;
            color: #57a101;
        }
    }
    > table {
        width: 100%;
        max-width: 522px;
        th.subject {
            min-width: 120px;
        }
        td.subject {
            text-align: right;
            padding-right: 5px;
        }
    }
    /* SEND MESSAGE: */
    > .note_header {
        width: 100%;
        max-width: 440px;
    }
    > .send {
        width: 100%;
        max-width: 440px;
        border: 2px solid #0d0c09;
        border-top-width: 0px;
        min-height: 416px;
        > .header {
            padding-bottom: 20px;
            border-bottom: 2px solid;
            > table {
                width: 100%;
                .green {
                    color: #71d000;
                    font-weight: bold;
                    font-size: 15px;
                }
                input {
                    width: 267px;
                    float: right;
                }
            }
        }
        > .body {
            padding-top: 20px;
            padding-bottom: 5px;
            > textarea {
                width: 100%;
                max-width: 412px;
                height: 258px;
            }
        }
        > .footer {
            margin-bottom: 10px;
        }
    }
    .underline {
        border-width: 0px;
        background: url(../images/small/underline.gif) repeat;
        font-family: Verdana, Arial, Helvetica, sans-serif;
        line-height: 16px;
    }
    /* NOTEBOOK: */
    > .plus-needed {
        color: gray;
        font-weight: bold;
        user-select: none;
    }
    > .note {
        width: 100%;
        max-width: 440px;
        min-height: 340px;
        border: 2px solid #0d0c09;
        border-top: 1px dashed silver;
        > .body {
            width: 100%;
            min-height: 240px;
            padding: 20px;
            > textarea {
                width: 100%;
                height: 240px;
                border: 1px dashed silver;
            }
        }
        > .footer {
            margin-bottom: 10px;
        }
    }
}
